;line-height:35px;font-weight:300}.gradient{height:20px;position:relative;width:100%;background:radial-gradient(#dddddd)}.gradient:before{content:"";display:block;position:absolute;top:0px;right:0;left:0;height:3px;background:linear-gradient(#dddddd)}.hashtags{color:#4e4d4d;font-family:"Domine",serif;font-size:14px;font-weight:700;margin-top:10px}#gihub-link{font-weight:lighter;background-color:#1523eb80;padding:3px 10px;font-size:12px;color:#fff;border-radius:10px;border-bottom:3px solid #1523ebdf}#app-link{font-weight:lighter;background-color:#3fa24f;padding:3px 10px;margin-left:2px;font-size:12px;color:#fff;border-radius:10px;border-bottom:3px solid #248a36}#app-link:hover,#gihub-link:hover{text-decoration:none;box-shadow:0px 2px 3px #4e4d4d}.project-img{height:auto;box-shadow:0px 3px 4px 0px rgba(8,8,8,.76);width:80%;border-radius:5px;border:4px rgba(27,26,26,.096) solid;opacity:.9}.row-projects{display:flex;justify-content:space-evenly}section{position:relative;background:none;color:#fff;text-align:center;justify-content:space-evenly}section:before{position:absolute;content:""}section.diagonal{background:rgba(234,234,240,.137)}.diagonal{z-index:1;padding:3em}.diagonal h2{color:#f9f5f1}.diagonal:before{transform:rotate(-4deg);transform-origin:3% 0;top:0;left:-28%;z-index:-1;width:150%;height:100%;background:#a3b9cc}.footer{background-color:#464646;bottom:0;width:100%;height:100%;padding:5px}.footer p{color:silver;font-family:"Lora",serif;font-weight:300;font-size:16px}.footer i{font-size:1.8em;color:silver}.copyright{padding:10px}.row-shared{height:100vh;align-items:center}@media screen and (max-width: 600px){.diagonal:before{left:-47%;width:183%;height:100%}.page-header{font-size:2.5em}.hero-header{font-size:3em}.hero-text{font-size:1em}.contact-form-header{font-size:16px;line-height:35px}.about-paragraph{font-size:16px;line-height:28px}.row-to-shrink{padding:40px}.row-projects{padding:0}.project-img{width:100%}}@media screen and (min-width: 1900px){.diagonal:before{left:-50%;width:200%;height:100%}}
